.main-nav__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .main-nav__toggle {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 50px;
      height: 50px;
      background-color: #0ad9c6; } }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 50%;
  transform: translateX(-50%);
  width: 22.5px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000; }

.main-nav--opened .main-nav__toggle {
  background: #ffffff;
  width: 50px;
  height: 50px; }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 13px;
    transform: translateY(-50%);
    width: 26px;
    height: 2px;
    background-color: rgba(20, 21, 23, 0.2);
    z-index: 100; }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg); }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg); }

@media (max-width: 1023px) {
  .main-nav__list {
    position: absolute;
    z-index: 100;
    width: 100vw;
    height: 100vh;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: start; }
  .main-nav__item {
    background-color: #f6f6f6;
    margin-bottom: 40px;
    padding: 15px 20%;
    position: relative;
    cursor: pointer; }
    .main-nav__item:first-child {
      margin-top: 50px; }
    .main-nav__item::after {
      content: "";
      position: absolute;
      top: 50%;
      right: 10%;
      width: 12px;
      height: 12px;
      border: 3px solid #48494d;
      border-bottom: transparent;
      border-left: transparent;
      transform: translateY(-50%) rotate(45deg); }
  .main-nav__link {
    font-weight: 400;
    color: #48494d;
    font-size: 20px; }
  .main-nav--opened .main-nav__list {
    display: bllock; }
  .main-nav--closed .main-nav__list {
    display: none; }
  .page-header--nojs .main-nav__list {
    display: block;
    top: -100vh; }
  .page-header--nojs .page-header__wrapper {
    margin-top: 100vh; } }

@media (max-width: 767px) {
